<template>
  <div id="topbar">
    <div class="wrapper">
    <span class="logo">  Resumer </span>

     <div class="actions">

       <div class="userActions" v-if="logined">
         <span class="welcome">您好，{{user.username}}</span>

         <el-button type="primary" @click.prevent="signOut">登出</el-button>
       </div>

       <div class="userActions" v-else>
         <el-button @click.prevent="signUpDialogVisible=true">注册</el-button>
         <el-button type="primary" @click.prevent="signInDialogVisible=true">登录</el-button>


       </div>

       <el-button :plain="true" type="info">保存</el-button>
        <el-button :plain="true" type="success" v-on:click="preview">预览</el-button>

     </div>
    </div>


    <MyDialog title="注册" :visible="signUpDialogVisible" @close="signUpDialogVisible = false">
       <SignUpForm @success="signIn($event)"  @cancel="cancel"/>
     </MyDialog>

     <MyDialog title="登录" :visible="signInDialogVisible"
       @close="signInDialogVisible = false">
       <SignInForm @success="signIn($event)" @cancel="cancel"/>
     </MyDialog>
 </div>
</template>

<script>
import MyDialog from "./MyDialog"
import SignUpForm from "./SignUpForm"
import SignInForm from './SignInForm'
import AV from '../lib/leancloud'
export default {
  name: 'Topbar',
  data(){
    return {
      signUpDialogVisible: false,
      signInDialogVisible: false,
    }
  },
  computed:{
    user(){
      return this.$store.state.user
    },
    logined(){
      return this.user.id
    }
  },
   components: {
    MyDialog,SignUpForm,SignInForm
  },
  methods:{
    preview() {
        this.$emit('preview');
     },
    signOut(){
      AV.User.logOut()
      this.$store.commit('removeUser')
    },
    signIn(user){
      this.signUpDialogVisible=false
      this.signInDialogVisible = false
      this.$store.commit('setUser',user)
    },
    cancel() {
        this.signUpDialogVisible = false;
        this.signInDialogVisible = false;
      }
  }

 }
</script>

<style scoped lang="scss">

#topbar{

    background:#ffffff;
    box-shadow:0 1px 3px 0 rgba(0,0,0,0.25);
        >.wrapper{
       min-width: 1024px;
       max-width: 1440px;
       margin: 0 auto;
       height:64px;
     }
     >.wrapper{
       display: flex;
       justify-content: space-between;
       align-items: center;
       padding: 0 16px;
     }
     .logo{
       font-size:24px;
       color:#000000;
     }
     }

    .button{ // 由于加了 scoped， 所以这个 button 选择器只在本组件内有效，不会影响其他组件
     width:72px;
     height:32px;
     border: none;
     cursor: pointer;
     font-size: 18px; // 设计稿上是 20px，看起来太大，就改成 18px 了
     background:#ddd;
     color: #222;
     text-decoration: none;
     display: inline-flex;
     justify-content: center;
     align-items: center;
     vertical-align: middle;

     &:hover{
       box-shadow: 1px 1px 1px hsla(0, 0, 0, 0.50);
     }
     &.primary{
       background-color:#02af5f;
       color: white;
     }
  }
   .actions{
     display: flex;
     .userActions{
       margin-right: 3em;
       .welcome{
        margin-right: .5em;
      }
     }
   }
</style>



