<template>
  <div class="dialogWrapper" v-show="visible">
    <div class="dialog">
      <header>
        {{title}}
        <span class="close" @click="close">×</span>
      </header>
      <main>
        <slot></slot>
      </main>
    </div>
  </div>

</template>


<script>
  export default{
    name: 'myDialog',
    props:['title','visible'],
    methods:{
      close(){
       this.$emit("close")
      }
    }
  }
</script>

<style scoped lang='scss'>
.dialogWrapper{
    position: fixed;
    background: hsla(0, 0%,0%,0.25);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;
  }
.dialog{
   border-radius: 5px;
   background: #fff;
   min-height: 4em;
   min-width: 10em;
   overflow: hidden;
    >header{
      padding: 8px 16px;
      display: flex;
      justify-content: space-between;
      background: #3386dd;
      color: #f0f0f0;
      .close {
          &:hover {
            cursor: pointer;
          }
        }
    }
    >main{
      padding: 16px;
    }
}
</style>



